





























































import Vue from 'vue'
export default Vue.extend({
  name: 'pimpAddDefka',

  data: () => ({
      dialog: false,
      inviteLink: '',
  }),
  methods: {
      send(){
          const shareData = {
              title: 'Регистрация дефки',
              text: 'Зарегестрировалась быстро, дефка!',
              url: this.inviteLink,
          }
          navigator.share(shareData)
      },
      drop(){
          this.dialog = false;
          //call api drop

          //call api get new link
          const res = 'https://vk.com';
          this.inviteLink = res;

      },
      copyToClipBoard(){
          const elem = document. createElement('textarea');
          elem.value = this.inviteLink;
          document.body.appendChild(elem);
          elem.select();
          document.execCommand('copy');
          document.body. removeChild(elem);
      }
  },
  mounted(){
      //req to api to get link
      const res = 'https://vuejs.org/guide/introduction.html#api-styles';
      this.inviteLink = res;
  }
})
