

































































































































































































































  import Vue from 'vue'
  import validationMixin from '@/plugins/vuetify'
import { register } from 'register-service-worker';

  export default Vue.extend({
    name: 'hookerRegistration',

    data: () => ({
			dialog: false,
      valid: false,
      firstname: '',
      lastname: '',
      nameRules: [
        (v: any) => !!v || 'Name is required',
        (v: any) => v.length <= 10 || 'Name must be less than 10 characters',
      ],
      email: '',
      emailRules: [
        (v: any) => !!v || 'E-mail is required',
        (v: any) => /.+@.+/.test(v) || 'E-mail must be valid',
      ],
			hugs: false,
			kissLight: false,
			kissWithTongue: false,
			selectAll: false,
			customService: [{
				enabled: false,
				name: '',
				price: '',
				}
			],
			photos: [],
			urlPhotos: [{string: ''}]
    }),

		methods: {
			addNewService(){
				this.customService.push({
				enabled: false,
				name: '',
				price: '',
				});
			},
			logFiles(){
				console.log(this.photos);
				this.urlPhotos = [];
				this.photos.forEach(photo => {
					let urlCreator = window.URL || window.webkitURL;
					const imageUrl = urlCreator.createObjectURL(photo);
					this.urlPhotos.push({string: imageUrl});
				})
			},
			register(){
				// api register call
				this.dialog = false;
				this.$emit('login', {userType: 0, token: 'blablabla'})
			}
		},
		
		watch: {
			photos: 'logFiles',
		}
  })
